import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(){
    super()
    this.state = {
      textInput: ''
    }
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(event) {
    const {name,value} = event.target
    this.setState({
      [name]: value
    })
  }
  render() {
    return (
      <div className="App">
        <header>
          <img src={logo} className="App-logo" alt="logo" />
          <h1>React Markdown Previewer</h1>
        </header>
        
        <h3>Editor</h3>
        <textarea id='editor' onChange={this.handleChange} name='textInput' value={this.state.textInput} />
<br></br><br></br>

        <h3>Preview</h3>
        <div id='preview'>
          {this.state.textInput}
        </div>
        
      </div>
    );
  }
}

export default App;